body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none; }

body,
button,
button:hover {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none; }

.jn-header {
  background-color: #ccc; }
  .jn-header .jn-navigator {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #000;
    border-radius: 0 0 3rem 3rem;
    flex-direction: row;
    height: 60px; }
    .jn-header .jn-navigator__action {
      width: 33.3%; }
    .jn-header .jn-navigator__link {
      padding-right: calc(10px + 50px);
      text-align: right;
      width: 33.3%; }
      .jn-header .jn-navigator__link--item {
        color: #fff;
        padding-left: 30px;
        text-decoration: none; }
        .jn-header .jn-navigator__link--item:hover {
          color: #ff5757;
          text-decoration: underline; }
      .jn-header .jn-navigator__link--active {
        color: #ff5757; }
    .jn-header .jn-navigator__title {
      text-align: center;
      width: 33.3%; }
      .jn-header .jn-navigator__title--item {
        color: #fff;
        font-weight: bold;
        text-decoration: none; }
        .jn-header .jn-navigator__title--item:hover {
          color: #ff5757;
          text-decoration: underline; }
  .jn-header .fab {
    margin-right: 5px; }
  .jn-header .fas {
    margin-right: 5px; }

@media screen and (max-width: 1000px) {
  .jn-header .jn-navigator__link--item {
    padding-left: 10px; }
  .jn-header .jn-navigator__link--title {
    display: none; } }

@media screen and (max-width: 450px) {
  .jn-header .jn-navigator__action {
    display: none; }
  .jn-header .jn-navigator__link {
    width: 50%; }
  .jn-header .jn-navigator__title {
    padding-left: calc(10px + 50px);
    width: 50%; } }

.jn-about__info {
  background: #fff;
  border: 2px solid #000;
  border-radius: .5rem;
  font-size: 20px;
  max-height: 200px;
  overflow: auto;
  padding: 10px;
  width: 35%;
  text-align: center; }
  .jn-about__info--link {
    color: #000;
    font-weight: bold; }
    .jn-about__info--link:hover {
      color: #ff5757; }

.jn-game-table {
  border: 3px solid #000;
  border-spacing: 0;
  table-layout: fixed; }
  .jn-game-table__body {
    display: block;
    max-height: calc(calc(calc(100vh - 60px * 2) - 100px - 80px) - 50px);
    max-width: calc(100vw - 50px * 2 - 10px * 2 - 50px);
    min-height: 85px;
    overflow: auto; }
  .jn-game-table__data {
    background-color: #e7e0b3;
    border: 1px solid #000;
    font-weight: bold;
    min-width: 30px;
    padding: 3px; }
    .jn-game-table__data--bottom {
      border-bottom: 3px solid #000;
      vertical-align: bottom; }
      .jn-game-table__data--bottom-val:not(:last-child) {
        display: block;
        padding-bottom: 3px; }
    .jn-game-table__data--col {
      text-align: center; }
    .jn-game-table__data--nonogram {
      font-weight: normal;
      height: 30px;
      text-align: center;
      width: 30px; }
      .jn-game-table__data--nonogram-black {
        background-color: #000; }
      .jn-game-table__data--nonogram-red {
        background-color: #f00; }
      .jn-game-table__data--nonogram-white {
        background-color: #fff; }
    .jn-game-table__data--right {
      border-right: 3px solid #000;
      vertical-align: center; }
      .jn-game-table__data--right-val:not(:last-child) {
        display: inline-block;
        padding-right: 7px; }
    .jn-game-table__data--row {
      text-align: right; }

.jn-help__info {
  background: #fff;
  border: 2px solid #000;
  border-radius: .5rem;
  font-size: 20px;
  max-height: 200px;
  overflow: auto;
  padding: 10px;
  width: 50%;
  text-align: justify; }
  .jn-help__info--paragraph {
    padding-right: 25px; }

@-webkit-keyframes blinker {
  50% {
    opacity: 0; } }

@keyframes blinker {
  50% {
    opacity: 0; } }

.jn-message {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: -webkit-linear-gradient(#9c9c9c, #000);
  background-image: linear-gradient(#9c9c9c, #000);
  border-top: 10px solid #000;
  height: 80px;
  text-shadow: 1px 1px 1px black; }
  .jn-message__title {
    margin: 0;
    text-align: center; }
    .jn-message__title--complete {
      color: #fff;
      font-size: 32px; }
    .jn-message__title--error {
      -webkit-animation: blinker 1s linear infinite;
              animation: blinker 1s linear infinite;
      color: #ff5757; }

@media screen and (max-width: 550px) {
  .jn-message__title--complete {
    font-size: 20px; } }

.jn-settings {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: -webkit-linear-gradient(#000, #9c9c9c);
  background-image: linear-gradient(#000, #9c9c9c);
  border-bottom: 10px solid #000;
  height: 100px; }
  .jn-settings__item {
    background-color: #000;
    border: 1px solid #fff;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
    height: 40px;
    width: 120px; }
    .jn-settings__item:first-child {
      border-radius: 0.5rem 0 0 0.5rem; }
    .jn-settings__item:last-child {
      border-radius: 0 0.5rem 0.5rem 0; }
    .jn-settings__item:hover {
      background-color: #fff;
      border: 1px solid #000;
      color: #000;
      font-size: 18px;
      font-weight: bold; }
    .jn-settings__item:disabled {
      cursor: not-allowed;
      opacity: .5; }
    .jn-settings__item--active {
      color: #ff5757;
      font-size: 18px; }
      .jn-settings__item--active:hover {
        background-color: #ff5757;
        color: #000; }

.jn-main {
  background-color: #ccc; }
  .jn-main .jn-content {
    border-left: 10px solid #000;
    border-right: 10px solid #000;
    height: calc(100vh - 60px * 2);
    margin: 0 50px; }
    .jn-main .jn-content .jn-container {
      display: flex;
      align-items: center;
      justify-content: center;
      background: url("/wallpaper.jpg") no-repeat center center/cover;
      height: calc(calc(100vh - 60px * 2) - 100px - 80px);
      width: 100%; }

.jn-footer {
  background-color: #ccc; }
  .jn-footer .jn-copyright {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #000;
    border-radius: 3rem 3rem 0 0;
    bottom: 0;
    color: #fff;
    flex-direction: row;
    height: 60px;
    width: 100%; }
  .jn-footer .fas {
    margin-right: 5px; }


